.book-style-none {
    float:left;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index:1010;
    height: .31rem;
    padding: .06rem 0;
    background: #333;
    border-bottom: 1px solid #ccc;
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
}

.book-style-none .selects {
    -webkit-box-flex: 1;
    -webkit-flex: 1;
    flex: 1;
    width: .1px;
    position: relative;
    font-weight: 600;
    letter-spacing: .04rem;
    outline: none;
    height: .29rem;
    margin: 0 .07rem;
    appearance: none;
    -webkit-appearance: none;
    border: 1px solid #6cd;
    border-radius: .06rem;
    color: #6cd;
    background: #333;
    background: url("/StaticResources/public_html/wings/_resources/img/wx/laikan/img/select_jianjiao.png") no-repeat 90% center;
    background-size: .11rem .08rem;
}

.book-style-none .selects .select-text {
    width: 100%;
    height: .29rem;
    line-height: .29rem;
    padding-left: .04rem;
    font-size:.14rem;
}

.book-style-none .selects ul {
    width: 100%;
    background: #fff;
    border: 1px solid #6cd;
    position: absolute;
    top: .30rem;
    color: #000;
    overflow: hidden;
    display: none;
}

.book-style-none .selects ul li {
    width: 100%;
    height: .29rem;
    line-height:.29rem;
    font-size:.14rem;
    color: #000;
    padding-left: .04rem;
}

.book-style-none .selects ul li.option-active {
    color: #6cd;
    background: #333;
}
.goto-top {
  width: .3rem;
  height: .3rem;
  position: fixed;
  z-index: 1000;
  top: 75%;
  right: 6%;
  display: none;
}

.goto-top img {
    display:block;
  width: .3rem;
  height: .3rem;
}





